
.infoDis_nav {
  width: 100%;
  height: 70px;
  line-height: 70px;
  background: #fff7eb;
  color: #8e8e8f;
  font-size: 20px;
}
.wrap {
  width: 1080px;
  margin: 0 auto;
}
.infoDis_nav span {
  width: 49.6%;
  float: left;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
}
.infoDis_nav .id2 {
  text-align: right;
}
.id-high {
  color: #f8b62b;
}
.infoDis_content .infoDis_box {
  display: none
}
.infoDis_box {
  padding: 94px 0;
}
.infoDis_box1 div {
  margin-bottom: 40px;
}
.infoDis_box1 div img {
  display: block;
  float: left;
  margin-right: 40px;
}
.infoDis_box1 div p {
  float: left;
  width: 888px;
  text-indent: 40px;
  font-size: 20px;
  line-height: 36px;
  color: #313131;
}
.infoDis_box1 div p span {
  color: #307bf2;
}
.infoDis_box2 img {
  display: block;
  margin: 0 auto;
}
.infoDis_box3 div {
  margin-bottom: 30px;
}
.infoDis_box3 div img {
  margin-left: 36%;
  margin-bottom: 40px;
  display: block;
}
.infoDis_box3 div span {
  float: left;
  display: block;
  text-align: center;
  color: #307bf2;
  font-size: 26px;
  line-height: 120px;
  width: 230px;
}
.infoDis_box3 div p {
  /* float: left; */
  text-indent: 40px;
  color: #666666;
  font-size: 17px;
  line-height: 40px;
  /* width: 688px; */
}



